body {
  background-color: black;
  font-family:'Fontin SmallCaps', sans-serif;
  background-image: url("../assets/images/320bg.png");
  background-size: cover;
  background-repeat: no-repeat;
  color: #a7906f;
}

main {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  align-items: center;
}

h3 {
  margin: 5px 0px 10px 0px;
}

.logo {
  position: absolute;
  top: 20px;
  height: 150px;
}

Button {
  font-family:'Fontin SmallCaps', sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 1000px;
  border-radius: 10px;
  background-color: #1d2025;
  background: linear-gradient(0deg, #1d2025, #42464d);
  margin: 110px auto 0px auto;
  padding-bottom: 20px;
  box-shadow: 0px 5px 2px rgb(0, 0, 0, 0.3);
}

.title {
  margin-top: 60px;
  margin-bottom: 0px;
  font-size: 40px;
  font-weight: 300;
  text-shadow: 2px 2px black;
}

.subtitle {
  margin: 0px 0px 20px 0px;
  font-weight: 100;
  text-shadow: 1px 1px black;
}

.background {
  background-color: #181a1f;
  margin: 5px;
  padding: 20px;
  border-radius: 5px;;
}

.progress-card {
  width: 815px;
}

.progressBar {
  width: 815px;
  height: 10px;
  border-radius: 12px;
  font-weight: 700;
  padding: 0px;
}

.progressBarBg {
  margin: 5px;
  background-color: rgb(56, 56, 56);
}

.progressBarFg {
  background: linear-gradient(45deg, #a7906f, #dbae6e);
  background-color: #dbae6e;
}

.total-points {
  margin: 5px;
  font-weight: 600;
}

.points-summary {
  display: flex;
  justify-content: space-between;
}

.main-container {
  display: flex;
  justify-content: space-around;
}

.challenge-tab-container {
  width: 320px;
  margin: 0px 0px 10px 0px;
  display: flex;
  justify-content: space-between;
}

.challenge-tab {
  padding: 3px 5px;
  background: linear-gradient(0deg, #a7906f, #dbae6e);
  border: 0px;
  border-radius: 3px;
  cursor: pointer;
  font-size: 16px;
}

.challenge-item {
  background-color: transparent;
  box-sizing: border-box;
  width: 320px;
  color: #a7906f;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 6px;
  border: 1px solid grey; 
  border-radius: 2px;
  margin: 2px 0px;
  cursor: pointer;
  font-size: 14px;
}

.challenge-item:hover {
  background-color: #21232a;
}

.challenge-item-check {
  background-color: rgb(0, 75, 0);
}

.challenge-item-check:hover {
  background-color: rgb(1, 58, 1);
}

.challenge-item-multi-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  margin: 0;
}

.challenge-item-multi {
  box-sizing: border-box;
  width: 225px; 
  background-color: transparent;
  color: #a7906f;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 6px;
  border: 1px solid grey; 
  border-radius: 2px;
  margin: 2px 0px;
  font-size: 14px;
}

.challenge-multi-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  border: 1px solid grey;
  background: transparent;
  color: #a7906f;
  cursor: pointer;
  padding: 0;
  font-weight: bold;
  font-size: 12px;
}

.challenge-multi-btn:hover {
  background-color: #21232a;
}

.slot-area {
  display: grid;
  grid-template-columns: 100px 50px 100px 50px 100px;
  grid-gap: 10px;
  justify-content: center;
}

.slot-card {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background-color: white;
  width: 100px;
  height: 150px;
  box-shadow: 0px 1px 2px rgb(0, 0, 0, 0.3);
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-size: 12px;
}

.slot-card-mainhand {
  grid-row: 2 
}

.slot-card-head {
  grid-row: 1;
  grid-column: 3;
}

.slot-card-body {
  grid-row: 2;
  grid-column: 3;
}

.slot-card-offhand {
  grid-row: 2;
  grid-column: 5;
}

.slot-card-gloves {
  grid-row: 3;
  grid-column: 1; 
  height: 100px;
}

.slot-card-boots {
  grid-row: 3;
  grid-column: 5; 
  height: 100px;
}

.slot-card-belt {
  grid-row: 3;
  grid-column: 3; 
  height: 50px;
}

.slot-card-plug {
  grid-row: 4;
  grid-column: 3; 
  height: 100px;
  background-size: contain;
}

.slot-card-plug.slot-card-normal {
  background-image: url("../assets/images/plug_normal_icon.png");
}

.slot-card-plug.slot-card-magic {
  background-image: url("../assets/images/plug_magic_icon.png");
}

.slot-card-plug.slot-card-unique {
  background-image: url("../assets/images/plug_unique_icon.png");
}

.slot-card-plug.slot-card-rare {
  background-image: url("../assets/images/plug_rare_icon.png");
}

.slot-card-plug.slot-card-normal:hover:after {
  content: url("../assets/images/plug_normal_2.png");
  position: absolute;
  left: -520px;
  bottom: -3px;
}

.slot-card-plug.slot-card-magic:hover:after {
  content: url("../assets/images/plug_magic.jpg");
  position: absolute;
  left: -520px;
  bottom: -3px;
}

.slot-card-plug.slot-card-rare:hover:after {
  content: url("../assets/images/plug_rare.jpg");
  position: absolute;
  left: -520px;
  bottom: -3px;
}

.slot-card-plug.slot-card-unique:hover:after {
  content: url("../assets/images/plug_unique.png");
  position: absolute;
  left: -520px;
  bottom: -3px;
}

.slot-card-ringleft {
  align-self: flex-end;
  grid-row: 2;
  grid-column: 2; 
  height: 50px;
  width: 50px;
}

.slot-card-ringright {
  align-self: flex-end;
  grid-row: 2;
  grid-column: 4; 
  height: 50px;
  width: 50px;
}

.slot-card-amulet {
  align-self: flex-end;
  grid-row: 1;
  grid-column: 4; 
  height: 50px;
  width: 50px;
}

.slot-title {
  margin: 0px;
  font-weight: 100;
}

.slot-card-locked {
  background-color: rgb(104, 3, 3);
}

.slot-card-normal {
  background-color: white;
}

.slot-card-magic {
  background-color: #9494fb;
}

.slot-card-rare {
  background-color: #f6f571;
}

.slot-card-unique {
  background-color: #9c541a;
}

.slot-level {
  display: flex;
  top: 5px;
  right: 5px;
  justify-content: center;
  align-items: center;
  position:absolute;
  font-size: 8px;
  min-width: 20px;
  min-height: 20px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.496);
  color: white;
}


.resetBtn {
  background-color:rgba(25, 25, 25, 0.496);
  color: #a7906f;
  margin-top: 15px;
  border-radius: 15px;
  border-width: 0;
  padding: 5px 15px 6px;
  cursor: pointer;
}